export const ENEMY_NUM = 7;
/***/
export const ENEMY_REPOP_TURN = 30;
/***/
export const GAME_CLEAR_FLOOR = 5;
/***/
export const LOG_MAX_LENGTH = 10;
/***/
export const MAP_LENGTH = 21;
